var componentActionsRecord = [];
var index = 0;
export default {
    saveNew: function (state, component) {
        componentActionsRecord[index] = { state: state, component: component };
        index++;
        return index - 1;
    },
    getRecordByIndex: function (inputIndex) { return componentActionsRecord[inputIndex]; },
    getComponentByIndex: function (inputIndex) { return (componentActionsRecord[inputIndex]
        ? componentActionsRecord[inputIndex].component
        : undefined); },
};
